<script setup lang="ts">
import { useI18n } from "vue-i18n";
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  ITenantAttribute,
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesOfferRelationValueConfiguration } from "./models";

defineProps<{
  attribute: ITenantAttribute;
  dataTypeInstance: IDataTypeInstance<IDataTypesOfferRelationValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesOfferRelationValueConfiguration>({
  set: ({ minRelations, maxRelations, ...rest }) =>
    ({
      minRelations: Number(minRelations),
      maxRelations: Number(maxRelations),
      ...rest,
    }) as IDataTypesOfferRelationValueConfiguration,
});

const { t } = useI18n();
</script>

<template>
  <ACol>
    <ATextField
      v-model.number="configuration.minRelations"
      :label="t('datahub-plugin.offerRelationValue.minRelations')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.maxRelations"
      :label="t('datahub-plugin.offerRelationValue.maxRelations')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="t('datahub-plugin.offerRelationValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>

<i18n>
